// PLUGINS IMPORTS //
import React, { useCallback, useContext } from 'react'
import { ThemeContext } from 'styled-components'
import Editor from 'react-simple-code-editor'
import Highlight, { defaultProps } from 'prism-react-renderer'

import lightTheme from 'prism-react-renderer/themes/nightOwlLight'
import darkTheme from 'prism-react-renderer/themes/nightOwl'

// COMPONENTS IMPORTS //
import * as Views from './styles'

// EXTRA IMPORTS //

// TYPE IMPORTS //
import { Language, ThemeType } from 'shared/types'
import type {
  Language as PrismLanguage,
  PrismTheme,
} from 'prism-react-renderer'

/////////////////////////////////////////////////////////////////////////////

type PropsType = {
  lang: Language
  value: string
  onValueChange: (value: string) => void
  onValueReset?: () => void
  shouldValueChange?: (value: string) => boolean
}

const CodeEditor = (props: PropsType) => {
  const theme = useContext(ThemeContext)

  const onCodeChange = useCallback(
    (newCode: string) => {
      if (props.onValueReset && /^(\s*)$/.test(newCode)) {
        props.onValueReset()
        return
      }

      if (!props.shouldValueChange || props.shouldValueChange(newCode))
        props.onValueChange(newCode)
    },
    [props.onValueChange, props.onValueReset, props.shouldValueChange]
  )

  const highlight = useCallback(
    (code: string) => (
      <Highlight
        {...defaultProps}
        code={code}
        // @ts-ignore
        theme={prismTheme[theme.type]}
        language={prismLanguage[props.lang]}
      >
        {({ tokens, getLineProps, getTokenProps }) => (
          <>
            {tokens.map((line, i) => (
              <div key={i} {...getLineProps({ line, key: i })}>
                {line.map((token, key) => (
                  <span key={key} {...getTokenProps({ token, key })} />
                ))}
              </div>
            ))}
          </>
        )}
      </Highlight>
    ),
    [props.lang, theme.type]
  )

  return (
    <Views.WrapperView>
      <Editor
        value={props.value}
        onValueChange={onCodeChange}
        highlight={highlight}
        tabSize={2}
      />
    </Views.WrapperView>
  )
}

export default CodeEditor

const prismLanguage: Record<Language, PrismLanguage> = {
  node: 'javascript',
  python: 'python',
}
const prismTheme: Record<ThemeType, PrismTheme> = {
  light: lightTheme,
  dark: darkTheme,
}
