// PLUGINS IMPORTS //
import React from 'react'

// COMPONENTS IMPORTS //
import * as Views from './styles'

// EXTRA IMPORTS //
import { ReactComponent as FirstIcon } from 'assets/icons/first.svg'
import { ReactComponent as LastIcon } from 'assets/icons/last.svg'
import { ReactComponent as NextIcon } from 'assets/icons/next.svg'
import { ReactComponent as PreviousIcon } from 'assets/icons/previous.svg'

// TYPE IMPORTS //

/////////////////////////////////////////////////////////////////////////////

type PropsType = {
  value: number
  onChange: (value: number) => void
  onNext: () => void
  onPrevious: () => void
  onLast: () => void
  onFirst: () => void
}

const ProgressBar: React.FC<PropsType> = (props) => {
  if (isNaN(props.value) || props.value < 0 || props.value > 1)
    throw new Error('Invalid value props')

  const handleClick = (e: React.MouseEvent<HTMLDivElement>) => {
    const rect = e.currentTarget.getBoundingClientRect()
    const relativeX = e.clientX - rect.left
    const newValue = relativeX / rect.width
    props.onChange(newValue)
  }

  return (
    <Views.WrapperView>
      <Views.ButtonView onClick={() => props.onFirst()}>
        <FirstIcon />
      </Views.ButtonView>
      <Views.ButtonView onClick={() => props.onPrevious()}>
        <PreviousIcon />
      </Views.ButtonView>
      <Views.WrapperBarView onClick={handleClick}>
        <Views.BarView widthPercent={props.value * 100} />
      </Views.WrapperBarView>
      <Views.ButtonView onClick={() => props.onNext()}>
        <NextIcon />
      </Views.ButtonView>
      <Views.ButtonView onClick={() => props.onLast()}>
        <LastIcon />
      </Views.ButtonView>
    </Views.WrapperView>
  )
}

export default ProgressBar
