// PLUGINS IMPORTS //
import React, { useState } from 'react'
import { ThemeProvider } from 'styled-components'
import { Toaster } from 'react-hot-toast'

// COMPONENTS IMPORTS //
import FunctionForm from 'components/function-form'
import TreeViewer from 'components/graph-viewer'
import Footer from './footer'

// EXTRA IMPORTS //
import * as Views from './styles'
import theme from 'styles/themes'
import { GlobalStyle, Themes } from 'styles'
import { DEFAULT_THEME_TYPE } from 'shared/config/constants'

// TYPE IMPORTS //
import { TreeViewerData, ThemeType, FunctionData, Language } from 'shared/types'

/////////////////////////////////////////////////////////////////////////////

type PropsType = {}
const App: React.FC<PropsType> = () => {
  const [themeType, setThemeName] = useState<ThemeType>(DEFAULT_THEME_TYPE)
  const [treeViewerData, setTreeViewerData] = useState<TreeViewerData>(null)
  const [treeViewerOptions, setTreeViewerOptions] = useState({ animate: false })
  const [isLoading, setIsLoading] = useState(false)

  const handleFunctionFormSubmit = async (
    lang: Language,
    functionData: FunctionData,
    options: { memoize: boolean; animate: boolean }
  ) => {
    setIsLoading(true)

    const data: TreeViewerData = {} as any
    // setTreeViewerData(data)
    // setTreeViewerOptions({ animate: options.animate })
    setIsLoading(false)
  }

  return (
    <ThemeProvider theme={Themes[themeType]}>
      <GlobalStyle />
      <Toaster
        position='top-left'
        reverseOrder={false}
        toastOptions={{
          duration: 5000,
          style: {
            background: theme[themeType].colors.foreground,
            border: `1px solid ${theme[themeType].colors.border}`,
            color: theme[themeType].colors.contrast,
            boxShadow: 'none',
          },
        }}
      />
      <Views.WrapperView>
        <Views.SidebarView>
          <FunctionForm
            onSubmit={handleFunctionFormSubmit}
            onThemeChange={setThemeName}
          />
        </Views.SidebarView>
        <Views.MainView>
          <TreeViewer
            isLoading={isLoading}
            data={treeViewerData}
            options={treeViewerOptions}
          />
          <Footer />
        </Views.MainView>
      </Views.WrapperView>
    </ThemeProvider>
  )
}

// STYLES //

export default App
