import styled, { keyframes } from 'styled-components'

const skRotate = keyframes`
  100% {
    transform: rotate(360deg);
  }
`
const skBounce = keyframes`
  0%, 100% {
    transform: scale(0.0);
  }
  50% {
    transform: scale(1.0);
  }
`

export const WrapperView = styled.div`
  margin: auto;
  width: 30px;
  height: 30px;
  position: relative;
  text-align: center;
  animation: ${skRotate} 2s infinite linear;
`

export const DotView = styled.div`
  width: 60%;
  height: 60%;
  display: inline-block;
  position: absolute;
  background-color: ${({ theme }) => theme.colors.contrast};
  border-radius: 100%;
  animation: ${skBounce} 2s infinite ease-in-out;
`

export const Dot1View = styled(DotView)`
  top: 0;
`

export const Dot2View = styled(DotView)`
  top: auto;
  bottom: 0;
  animation-delay: -1s;
`
