// PLUGINS IMPORTS //
import React from 'react'

// COMPONENTS IMPORTS //
import * as Views from './styles'
import Vertice from './vertice'
import DirectedEdge from './directed-edge'

// EXTRA IMPORTS //

// TYPE IMPORTS //
import { Point, VerticesData, EdgesData } from 'shared/types'

/////////////////////////////////////////////////////////////////////////////

type PropsType = {
  time: number
  bottomRight: Point
  vertices: VerticesData
  edges: EdgesData
}

const Graph: React.FC<PropsType> = (props) => {
  return (
    <Views.Svg viewBox={`0 0 ${props.bottomRight[0]} ${props.bottomRight[1]}`}>
      {Object.entries(props.edges).map(([key, data]) => {
        const [u, v] = JSON.parse(key)

        return (
          props.time >= data.timeRange[0] &&
          props.time <= data.timeRange[1] &&
          props.vertices[u]?.coord &&
          props.vertices[v]?.coord && (
            <DirectedEdge
              key={key}
              start={props.vertices[u].coord}
              end={props.vertices[v].coord}
              label={data.label}
              highlight={data.timeRange[0] === props.time}
            />
          )
        )
      })}
      {Object.entries(props.vertices).map(([key, data]) => {
        return (
          props.time >= data.times[0] && (
            <Vertice
              key={key}
              center={data.coord}
              label={data.label}
              highlight={
                data.times.some((t) => t === props.time)
                  ? 'current'
                  : data.memoized
                  ? 'memoized'
                  : 'none'
              }
            />
          )
        )
      })}
    </Views.Svg>
  )
}

export default Graph
