// PLUGINS IMPORTS //
import React from 'react'

// COMPONENTS IMPORTS //
import * as Views from './styles'

// EXTRA IMPORTS //
import { useScaleDown } from 'shared/hooks'
import { VERTEX_RADIUS } from 'components/graph-viewer/graph/constants'

// TYPE IMPORTS //
import { Point } from 'shared/types'

/////////////////////////////////////////////////////////////////////////////

type PropsType = {
  center: Point
  label?: string
  highlight: 'current' | 'memoized' | 'none'
}

const Vertice: React.FC<PropsType> = (props) => {
  const textRef = React.useRef<SVGTextElement>(null)
  useScaleDown(textRef, props.label)

  return (
    <Views.WrapperView highlight={props.highlight}>
      <Views.CircleView
        cx={props.center[0]}
        cy={props.center[1]}
        r={VERTEX_RADIUS}
      />
      <Views.TextView x={props.center[0]} y={props.center[1]} ref={textRef}>
        {props.label}
      </Views.TextView>
    </Views.WrapperView>
  )
}

export default Vertice
