// PLUGINS IMPORTS //
import React, { useState, useEffect } from 'react'

// COMPONENTS IMPORTS //
import { Graph, ProgressBar, LogBar, Loader } from './components'

// EXTRA IMPORTS //
import * as Views from './styles'
import { useInterval } from 'shared/hooks'

// TYPE IMPORTS //
import { TreeViewerData } from 'shared/types'

/////////////////////////////////////////////////////////////////////////////

type PropsType = {
  data: TreeViewerData
  isLoading: boolean
  options: { animate?: boolean }
}

const TreeViewer: React.FC<PropsType> = ({
  options: { animate = false },
  ...props
}) => {
  const [isUpdating, setIsUpdating] = useState(false)
  const [time, setTime] = useState(0)
  const [times, setTimes] = useState(1)

  if (time < 0 || time > times)
    throw Error(
      'Invalid state: `time` should never be outside the range from 0 to `times`'
    )

  useEffect(() => {
    setTime(0)
    if (props.data !== null) {
      setIsUpdating(true)
      setTimes(props.data.times)
    }
  }, [props.data])

  useEffect(() => {
    if (props.isLoading) {
      setTime(0)
      setIsUpdating(false)
    }
  }, [props.isLoading])

  useInterval(
    () => {
      if (time >= times) setIsUpdating(false)
      setTime((time) => (animate ? Math.min(time + 1, times) : times))
    },
    isUpdating ? DELAY_IN_MS : null
  )

  return (
    <Views.WrapperView>
      <ProgressBar
        value={time / times}
        onChange={(value: number) => {
          setTime(Math.round(value * times))
          setIsUpdating(false)
        }}
        onNext={() => setTime((time) => Math.min(time + 1, times))}
        onPrevious={() => setTime((time) => Math.max(time - 1, 0))}
        onFirst={() => setTime(0)}
        onLast={() => setTime(times)}
      />
      {props.isLoading ? (
        <Loader />
      ) : props.data === null ? (
        <Views.LogoIconView />
      ) : (
        <>
          <LogBar text={props.data.logs[time]} />
          <Graph
            time={time}
            vertices={props.data.verticesData}
            edges={props.data.edgesData}
            bottomRight={props.data.svgBottomRight}
          />
        </>
      )}
    </Views.WrapperView>
  )
}
const DELAY_IN_MS = 200

export default TreeViewer
